import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Home from "./js/home.js";
import Nav from "./js/nav.js";
import PG from "./js/pg/putyagang.js";
import TB from "./js/tb/teambatak.js";
import Personal from "./js/personal.js";
import Kepler from "./js/kepler/kepler.js";
import AllMemories from "./js/allmemories.js";

ReactDOM.render(
    <>
        <Router>
            <Nav />
            <Switch>
                <Route
                    path={process.env.PUBLIC_URL + "/"}
                    exact
                    component={Home}
                />
                <Route
                    path={process.env.PUBLIC_URL + "/putyagang/person/:id"}
                    component={Personal}
                />
                <Route
                    path={process.env.PUBLIC_URL + "/putyagang/person/:id##"}
                    component={Personal}
                />
                <Route
                    path={process.env.PUBLIC_URL + "/putyagang"}
                    component={PG}
                />
                <Route
                    path={process.env.PUBLIC_URL + "/putyagang##"}
                    component={PG}
                />
                <Route
                    path={process.env.PUBLIC_URL + "/teambatak"}
                    component={TB}
                />
                <Route
                    path={process.env.PUBLIC_URL + "/kepler"}
                    component={Kepler}
                />
                <Route
                    path={process.env.PUBLIC_URL + "/memories"}
                    exact
                    component={AllMemories}
                />
            </Switch>
        </Router>
    </>,
    document.getElementById("root")
);
